@use "global" as *;

.header {
  position: fixed;
  top: 0;
  left: 0;
	right:0;
  width: 100%;
	margin-inline:auto;
  height: rem(80);
  z-index: 200;

	transition: all 0.6s ease-out;
	@include mq(md) {
			height: rem(80);
	}
}
.header--top {
	background-color: $greenpale;
  transition: background-color 0.3s ease-in-out;
}
.header--top.is-scroll {
    background-color: rgba(0, 0, 128, 1);
}

.header--sub {
    background-color: rgba(0, 0, 128, 1);
}



.header__inner {
	margin-inline:auto;
  width: 100%;
  height: inherit;
	display: flex;
  justify-content: space-between;
  align-items: center;
	padding:0 rem(25);

	@include mq(md) {

	}
}
.header__logo {
	position: relative;
  width: rem(180);
	height: auto;
	display: flex; // 
  justify-content: center; // 
  align-items: center; // 
	@include mq(md) {
			width: rem(200);
	}
}

.header__logo-img {
	width: 100%;
  height: auto;
  position: absolute; /*  */
  left: 50%;
  top: 50%; /*  */
  transform: translate(-50%, -50%); /*  */
  transition: opacity 0.3s ease-in-out; /*  */

}

/* PC/SP */
.header__logo-img--after,
.header__logo-img--drawer {
	display: none;
	visibility: hidden; /* Safari */
	opacity: 0; /* Safari */
	position: absolute; /*  */
}

/* PCFV */
.is-scroll .header__logo-img--before {
	display: none; /*  */
	visibility: hidden;
	opacity: 0;
	position: absolute;
}

.is-scroll .header__logo-img--after {
	display: block; /*  */
	visibility: visible;
	opacity: 1;
}

/* SP */
.is-drawerActive .header__logo-img--before {
	display: none; /*  */
	visibility: hidden;
	opacity: 0;
	position: absolute;
}

.is-drawerActive .header__logo-img--drawer {
	display: block; /*  */
	visibility: visible;
	opacity: 1;
}
