@use "global" as *;

.fv {
  position: relative;
  margin-inline: auto;
  width: 100%;
  height: 100vh;
  overflow: hidden;
	@include mq(md) {
		display:flex;
		justify-content: center;
		align-items: center;
	}
}

.fv__slider-wrap {
	overflow: hidden;
}

.fv__slider {
	margin-inline:auto;
	width:100%;
}

.fv__slider-img {
	margin-inline: auto;
  width: 100%;
  height: 100vh;
	@include mq(md) {
		overflow: hidden;
		height:auto;
  }
}
.fv__slider-img img {
  display: block;
	width:100%;
	height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  object-fit: cover;
}

.fv__copy{
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -40%);
	display: flex; /*  */
  flex-direction: column; /*  */
  align-items: center; /*  */
  color: #fff;
  z-index: 2;
	width: 100%;

	@include mq(md) {
	position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -35%);
	}
}
.fv__main-title {
	color: #fff;
	font-size: rem(40);
			font-family: $second-font-family;
			font-weight: $regular;
			line-height: 2;
			letter-spacing: 0.54em;
			text-indent: 0.54em;
			background: rgba(0, 0, 0, 0.15);
			display: inline-block;
			@include mq(md) {
					font-size: rem(80);
					line-height: 1.7;
			}
}
.fv__sub-title {
	color: #fff;
	font-size: rem(20);
			font-family: $base-font-family;
			font-weight: $regular;
			line-height: 2;
			letter-spacing: 0;
			margin-top: rem(20);
			background: rgba(0, 0, 0, 0.15);
			display: inline-block;
			padding: rem(10) rem(20);
			@include mq(md) {
				margin-top: 0;
				line-height: 2;
			}
}

/* GranLily  */
.fv__description {
  position: absolute; /*  */
  top: 70%; /*  */
  left: 50%; /*  */
  transform: translate(-50%, -70%); /*  */
  width: 100%;
  // text-align: center;
  color: white; /*  */
  z-index: 10; /*  */
	text-align: center; /*  */
  display: flex; /*  */
  flex-direction: column; /*  */
  align-items: center; /*  */
}

/*  */
.text-line {
    font-size: rem(14);
    font-family: $base-font-family;
    font-weight: $regular;
    color: #ffffff; /*  */
    background: rgba(0, 0, 0, 0.15); /*  */
    padding: rem(10) rem(20);
    border-radius: rem(5);
  	display: inline-block;
		line-height: 2; /*  */
    opacity: 0; /*  */
		@include mq(md) {
				font-size: rem(16);
		}
}
